import { React, useEffect, useState } from "react";
import { Button } from "react-bootstrap";

const Checkout = (props) => {
  const [checkoutChart, setCheckoutCart] = useState(props.cart);
  const [totalAmount, setTotalAmount] = useState(0);

  useEffect(() => {
    setCheckoutCart(props.cart);
    checkoutChart.forEach((item) => {
      const total = parseFloat(item.price);
      setTotalAmount((prev) => prev + total);
    });
  }, [props.cart]);

  return (
    <div className="checkout-cart">
      <span
        onClick={() => props.setCartClicked()}
        style={{
          padding: "5px 10px",
          cursor: "pointer",
          fontSize: "x-large",
          position: "absolute",
          right: "0",
          top: "-9px",
        }}
      >
        x
      </span>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          flexDirection: "column",
          justifyContent: "center",
          gap: "10px",
          marginTop: "20px",
        }}
      >
        <div style={{ display: "flex", gap: "20px", alignItems: "baseline" }}>
          <h4>Your Cart</h4>
          <span
            style={{
              background: "gray",
              borderRadius: "30px",
              padding: "0px 7px",
              color: "white",
            }}
          >
            {props.cartLength}
          </span>
        </div>
        <div
          className="cart-items-container"
          style={{
            border: "1px solid gray",
            borderRadius: "10px",
            width: "70%",
          }}
        >
          {checkoutChart.map((item) => {
            return (
              <div
                key={item.id + Math.floor(Math.random() * 10000)}
                style={{
                  display: "flex",
                  gap: "10px",
                  padding: "20px",
                  justifyContent: "space-between",
                  borderBottom: "1px solid gray",
                  flexWrap: "wrap",
                }}
              >
                <p style={{ color: "black" }}>{item.name}</p>
                <p>${item.price}</p>
              </div>
            );
          })}
          <div
            style={{
              display: "flex",
              gap: "10px",
              padding: "20px",
              justifyContent: "space-between",
              flexWrap: "wrap",
              wordBreak: "break-all",
              color: "black",
            }}
          >
            <p>Total (USD):</p>
            <p>${totalAmount}</p>
          </div>
        </div>
        <div
          style={{
            marginBottom: "20px",
            display: "flex",
            gap: "10px",
            flexWrap: "wrap",
            justifyContent: "center",
          }}
        >
          <Button>Checkout</Button>
          <Button>Empty Cart</Button>
        </div>
      </div>
    </div>
  );
};

export default Checkout;
